    </div>
    <div id="ddc-container" class="project-container">
      <DDCLogo id="ddc-logo" class="project-logo" />
      <div id="ddc-overlay" class="project-overlay">
        <p id="ddc-intro" class="intro">
          "Elegance and tradition with a modern twist"
        </p>
        <p class="project-description">
          Built to withstand time, the Delle Donne Consort-website uses heavy contrast, straight lines and high-quality imagery to stand out.
