<script>
import $ from 'jquery'
import ToBeLogo from '@/components/project-logos/ToBeLogo.vue'
import DDCLogo from '@/components/project-logos/DDClogo.vue'

export default {
  components: {
    ToBeLogo,
    DDCLogo
  },
  methods: {
    openOverlay(id) {
      if (id === "tobe-logo") {
        console.log('tobe clicked')
        $('#tobe-overlay').animate({
          left: '100%'
        }, 400);
        $('#ddc-container').animate({
          left: '100%'
        }, 400);
        setTimeout(function () {
          $('#tobe-overlay *').css('opacity', '1');
        }, 200);
      } else if (id === "ddc-logo") {
        console.log('ddc clicked')
        setTimeout(function () {
          $('.logo .small').css('background-color', '#F5F5F5');
        }, 350);
        $('#ddc-overlay').animate({
          right: '100%'
        }, 400);
        $('#tobe-container').animate({
          left: '-50%'
        }, 400);
        setTimeout(function () {
          $('#ddc-overlay *').css('opacity', '1');
        }, 200);
      }
    },
    closeOverlay(id) {
      if (id === "tobe-close") {
        $('#tobe-overlay *').css('opacity', '0');
        setTimeout(function () {
          $('#tobe-overlay').animate({
            left: '0%'
          });
          $('#ddc-container').animate({
            left: '50%'
          });
        }, 100);
      } else if (id === "ddc-close") {
        setTimeout(function () {
          $('.logo .small').css('background-color', '#88D483');
        }, 150);
        $('#ddc-overlay *').css('opacity', '0');
        setTimeout(function () {
          $('#ddc-overlay').animate({
            right: '0%'
          });
          $('#tobe-container').animate({
            left: '0%'
          });
        }, 100);
      }
    }
  },
  mounted() {
    var vm = this;
    $('.logo .small').css('background-color', '#88D483');
    $('.tosite-btn').hover(function(){
      $(this).children("svg").addClass('animate-tosite-btn');
    }, function() {
      $(this).children("svg").removeClass('animate-tosite-btn')
    })
    $('.close-project-btn').click(function() {
      var id = $(this).attr('id');
      console.log('close clicked');
      vm.closeOverlay(id);
    })
    $('.project-logo').click(function() {
      var id = $(this).attr('id');
      vm.openOverlay(id);
    });
  }
}
</script>

<style>
  .project-container {
    position: absolute;
