    color: #102542;
  }

  #ddc-overlay .project-description {
    color: #636363;
  }

  .tosite-btn > svg {
    position: relative;
    left: 0px;
    bottom: 2px;
    transition: fill .3s ease;
  }

  .animate-tosite-btn {
    animation: animate-arrow .5s ease forwards;
  }

  @keyframes animate-arrow {
    25% {
      left: -5px;
    } 75% {
      left: 15px;
    } 100% {
      left: 0px;
    }
  }


</style>
